<template>
    <div class="swipBody">
        <div class="BtLeft" @click="pagesub">
            <div>
            </div>
        </div>
        <img :src="imgPath(index)" alt="图片加载失败" :class="imgClass(index)" v-for="(item,index) in list" :key="index" />
        <div class="BtRight" @click="pageAdd">
            <div>

            </div>
        </div>
        <div class="bar">
            <span :style="{'background-color':barColor(i)}" v-for="(item,i) in list" :key="i"></span>
        </div>   
    </div>    
</template>
<script>

export default {
    data(){
        return{
            page:0,
            list:[1,2,3,4,5,6,7,8],
            timer:undefined
        }
    },
    computed:{
        barColor(i){
            return function(i){
                return i==this.page?"red":"white";
            }
        },
        imgClass(i){
            return function(i){
                let length=this.list.length-1;//最後一張的索引
                if(this.page==0){
                    //展示第一張特殊處理
                    if(this.page==i){
                        return 'main';
                    }else if(i==length){
                        return 'left'
                    }else if(i==(this.page+1)){
                        return 'right'
                    }else{
                        return 'other'
                    }
                }else if(this.page==length){
                    //最後一張特殊處理
                    if(this.page==i){
                        return "main"
                    }else if(i==0){
                        return 'right'
                    }else if(i==length-1){
                        return 'left'
                    }else{
                        return 'other'
                    }
                }else{
                    if(i==this.page){
                        return 'main'
                    }else if(i==(this.page+1)){
                        return 'right'
                    }else if(i==(this.page-1)){
                        return 'left'
                    }else{
                        return 'other'
                    }
                }
            }
        },
        imgPath(i){
            return function(i){
                return `./static/img/che${i}.jpg`
            }
        }
    },
    mounted(){
        this.changePage();
    },
    methods:{
        pageAdd(){
            if(this.timer)clearInterval(this.timer);
            let length=this.list.length;
            if(this.page==length-1){
                this.page=0;
            }else{
                this.page++;
            }
        },
        pagesub(){
            let length=this.list.length;
            if(this.timer)clearInterval(this.timer);
            if(this.page<=0){
                this.page=length-1;
            }else{
                this.page--;
            }
        },
        //自动切换图片
        changePage(){
            let length=this.list.length-1;
            let page=this.page;
            if(this.timer)clearInterval(this.timer);
            this.timer=setInterval(()=>{
                if(page==length){
                    this.page=0;
                }else{
                    this.page++;
                }
            },5000);
        }
    },
    updated(){
        this.changePage();
    },
    destroyed(){
        if(this.timer)clearInterval(this.timer);
    }
}
</script>
<style scoped>
.swipBody{
    position: relative;
    height: 220px;
    width: 725px;
    overflow: hidden;
}
.BtLeft,.BtRight{
    position: absolute;
    height: 100%;
    width: 20px;
    display: none;
}
.BtLeft{
    left: 0px;
    z-index: 1;
}
.BtLeft>div{
    position:absolute;
    top:50%;
    transform: translateY(-50%);
    height: 16px;
    width: 16px;
    background: url('../../../static/img/left.png') no-repeat;
}
.BtLeft>div:hover{
    background: url('../../../static/img/lefthover.png') no-repeat;
}
.BtRight{
    right: 0px;
}
.BtRight>div{
    position:absolute;
    top:50%;
    transform: translateY(-50%);
    height: 16px;
    width: 16px;
    background: url('../../../static/img/right.png') no-repeat;
}
.BtRight>div:hover{
    background: url('../../../static/img/righthover.png') no-repeat;
}
.swipBody:hover .BtLeft{
    display: inline-block;
}
.swipBody:hover .BtRight{
    display: inline-block;
}
.other{
    position: absolute;
    width: 540px;
    height: 200px;
    left: 100%;
    z-index: -1;
    /*防止滑动时超出*/
    transform: scale(0.1);
    transition: 0.4s;
}
.main{
    position: absolute;
    width: 540px;
    height: 200px;
    z-index: 1;
    left: 50%;
    transform: translateX(-50%);
    transition: 0.4s;
}
.left{
    position: absolute;
    width: 540px;
    height: 200px;
    left: 0px;
    transform-origin: -50% 100%;
    transform: scale(0.9);
    transition: 0.4s;
}
.right{
    position: absolute;
    width: 540px;
    height: 200px;
    transform: scale(0.9);
    left: 540px;
    transform-origin: 50% 100%;
    transition: 0.4s;
}
.bar{
    position:absolute;
    bottom: 2px;
    left: 50%;
    transform: translateX(-50%)
}
.bar span{
    display: inline-block;
    height: 4px;
    width: 20px;
    margin: 0px 5px;
}
</style>


